






// Vue
import { Component, Prop, Vue } from 'vue-property-decorator';
import LoginCard from '@/features/user/user-login-card.vue';
import Notice from '@/components/notice.vue';
import wrap from '@vue/web-component-wrapper';

// Features
import { DeviceData } from '@/features/devices';
// Store
import { Status } from '@/store/user';


// Services & helpers
import {log} from '@/services/logger';

const device: Partial<DeviceData> = {
    key: '123',
    color: '#ff0000',
    name: 'twilightSound',
};

@Component({
    components: {
        LoginCard,
    },
})
export default class ItemperDevice extends Vue {
    public store = Vue.$store.user;

    public login(status: Status) {
        log.debug('itemperDevice.login: status=' + Status[status]);
    }
    public provisionDevice() {
        log.debug('itemperDevice.provisionDevice');
        this.$emit('device', device);
    }
    public swap() {
        log.debug('itemperDevice.swap');
    }
}

const itemperDeviceElement = wrap(Vue, ItemperDevice);
window.customElements.define('itemper-device', itemperDeviceElement);
